import hexSha1 from 'hex-sha1';
import uniLoadMore from "@/components/uni-load-more.vue"
import {ChangePageTitle} from '../../../title.js'
export default {
	data() {
		return {
			title: '我的任务',
			back1: true,
			accessToken: '64e9275246c6545a3b5d3ad060fe4c516f44d556',
			appsercert: 'MTExMTEx',
			sha: '',
			keys: [],
			lists: [],
			currentid2:"",
			currentid: 0,
			page: 1,
			id: '',
			name: "",
			hide: false,
			show: true,
			status: 'more',
			isShow: false,
			src: '../../static/image/down.png',
			show1:false,
			show2:false,
			show3:false,
			show4:false,
		}
	},
	components: {
		uniLoadMore
	},
	methods: {
		getlist() {
			var signStr =
				"access-token=" +
				this.accessToken +
				"&appsercert=" +
				this.appsercert +
				"&page=" +
				this.page;
			console.log(signStr)
			this.sha = hexSha1(signStr);
			console.log(this.sha)
			this.$axios(this.$baseUrl.tasklist, {
					'access-token': this.accessToken,
					sign: this.sha,
					page: this.page
				})
				.then(res => {
					console.log(res)
					uni.hideLoading()
					// this.keys = res.data.teamtask
					this.lists = res.data.teamtask
					this.msg = this.lists[0].name
					this.keys = this.lists[0].task
					for (var i = 0; i < this.keys.length; i++) {
						var d2 = new Date();
						var d1 = new Date(this.keys[i].t_addTime);
						// console.log(Math.floor(parseInt(d2 - d1) / 3600000/24)); //两个时间相差的小时数
						// console.log(Math.floor(parseInt(d2 - d1) / 3600000%24))
						this.$set(this.keys[i], 'guoqi', Math.floor(parseInt(d2 - d1) / 3600000 / 24) + '天' + Math.floor(parseInt(d2 -
							d1) / 3600000 % 24) + '小时')
					}
				}, (error) => {
					console.log(error)
				})
		},
		todetail(id) {
			console.log(id)
			// uni.navigateTo({
			// 	url: '/pages/cluedetail/index?id=' + id
			// })
		},
		// 选择
		select() {
			this.hide = !this.hide
			this.show = !this.show
			if (this.hide) {
				this.src = '../../static/image/up.png'
			} else {
				this.src = '../../static/image/down.png'
			}
		},
		clk(index, id, name) {
			this.currentid = index
			this.id = id
			this.msg = name
			this.hide = !this.hide
			this.show = !this.show
			if (this.hide) {
				this.src = '../../static/image/up.png'
			} else {
				this.src = '../../static/image/down.png'
			}
		},
		check(index){
			this.currentid2 = index
		},
	},
	onReachBottom() {
		this.status = "loading";
		console.log("我被拉到底部了");
		setTimeout(() => {
			this.page++;
			console.log(this.page);
			this.getlist();
		}, 2000)
	},
	mounted() {
		var list =  uni.getStorageSync('login')
		if(list){
			// alert(JSON.stringify(list))
			this.accessToken = list.access_token
			this.appsercert = list.appsercert
			// alert(this.appsercert)
			// alert(this.accessToken)
		}
		uni.showLoading({
			title: '加载中'
		})
		this.getlist()
	},
	onShow() {
		document.title = '我的任务'
		ChangePageTitle('我的任务')
	}
}
